let json = 'dataContent.json'

async function dataContent() {
    let response = await fetch(json);
    let datas = await response.json();
    let data = document.getElementById("bookList")
    datas.forEach((datas) => {
      data.innerHTML =
        data.innerHTML +
        `<tr>
             <td>${datas.title}</td>
             <td>${datas.author}</td>
        </tr>
        `;
    })
}

function nextPage(){
    window.location = "bookDetails.html";
}

dataContent()